@keyframes show-mobile-btn {
  0% { opacity: 0; }
  80%  { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes hide-mobile-btn {
  0% { opacity: 1; }
  10%  { opacity: 0; }
  80% { opacity: 0; }
  90% { opacity: 0.8; }
  100% { opacity: 1; }
}

@keyframes load {
	0% {
		left: 0;
		height: calc(var(--preloader-animation-element-size) * 2);
		width: var(--preloader-animation-element-size);
	}
	50% {
		height: calc(var(--preloader-animation-element-size) / 2.1);
		width: calc(var(--preloader-animation-element-size) * 3);
	}
	100% {
		left: calc(var(--preloader-width) - var(--preloader-animation-element-size));
		height: calc(var(--preloader-animation-element-size) * 2);
		width: var(--preloader-animation-element-size);
	}
}
