.navigation__inner-page {
  align-self: flex-end;
  width: max-content;
  position: relative;
  display: flex;
  justify-content: end;
  align-items: center;
  padding-left: var(--nav-title-size);
  font-family: var(--basic-font);
  font-size: var(--nav-title-size);
  font-weight: var(--nav-title-weight);
  color: var(--sub-text-color);
}
.navigation__inner-page::before {
  position: absolute;
  top: -1px;
  left: 0;
  color: var(--main-color);
  font-size: var(--nav-title-size);
  font-weight: var(--nav-title-weight);
  content: '>';
}
.main {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  grid-area: main;
  align-self: start;
  background-color: var(--back-color);
}
.main--index {
  background-image: url('../img/henkel-duesseldorf-logo.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
}
@media (max-width: 900px) {
  .main--index {
    background-position: 85% 0%;
  }
}
.footer {
  padding: var(--footer-top-padding) var(--standart-side-padding);
  grid-area: footer;
  align-self: start;
}
.footer__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.footer__wrap > .footer__suport {
  align-items: flex-end;
}
@media (max-width: 900px) {
  .footer__wrap {
    flex-direction: column;
  }
  .footer__wrap > .footer__suport {
    align-items: center;
  }
}
.footer__suport {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: var(--basic-font);
  font-size: var(--footer-font-size);
  font-weight: var(--footer-font-weigth);
  color: var(--sub-text-color);
}
.footer__sup-link {
  color: var(--sub-text-color);
}
.footer__sup-link:hover,
.footer__sup-link:focus {
  color: var(--main-color);
}
.offer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  overflow: auto;
  background-color: var(--back-color);
}
.crumbs {
  width: 100%;
  padding: var(--crumbs-top-padding) var(--standart-side-padding);
  z-index: var(--index-crumbs);
}
.crumbs__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 5px;
  list-style: none;
}
.crumbs__item {
  position: relative;
  padding-right: var(--crumbs-font-size);
}
.crumbs__item:not(:last-child):after {
  position: absolute;
  height: min-content;
  top: 0px;
  right: 0;
  color: var(--crumbs-arrow-color);
  font-size: var(--crumbs-font-size);
  font-weight: var(--crumbs-font-weight);
  content: '>';
}
@media (min-width: 900px) {
  .crumbs__item:hover .crumbs__dropdown,
  .crumbs__item:focus-within .crumbs__dropdown {
    opacity: 1;
    pointer-events: all;
    transition: all ease 0.3s;
    transform: translateX(0px);
  }
}
.crumbs__crumb {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transition: var(--crumbs-hover-animation);
  text-decoration: none;
}
.crumbs__crumb--dropdown-arrow {
  padding-right: 12px;
  cursor: pointer;
}
.crumbs__crumb--dropdown-arrow:after {
  content: '';
  position: absolute;
  top: 40%;
  right: 0;
  border-top: 8px solid var(--sub-text-color);
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
}
@media (max-width: 900px) {
  .crumbs__crumb--dropdown-arrow {
    padding-right: 0;
  }
  .crumbs__crumb--dropdown-arrow:after {
    display: none;
  }
}
.crumbs__dropdown {
  position: absolute;
  min-width: 120px;
  opacity: 0;
  pointer-events: none;
  transform: translateX(-40px);
  transition: all ease 0.2s;
  padding: var(--crumbs-top-padding);
  list-style: none;
  background-color: var(--back-color);
  box-shadow: var(--dropdown-crumbs-shadow);
  z-index: 1;
  border-radius: 8px;
  box-shadow: 0.6rem 0.3rem 0.3rem 0.1rem #c8d0e7, -0.3rem -0.3rem 0.9rem #fff;
}
.crumbs__droped-link {
  padding: 4px;
  text-decoration: none;
  display: flex;
  justify-content: start;
  transition: var(--crumbs-hover-animation);
}
.crumbs__text {
  font-family: var(--basic-font);
  font-size: var(--crumbs-font-size);
  font-weight: var(--crumbs-font-weight);
  color: var(--sub-text-color);
  user-select: none;
  white-space: nowrap;
}
.crumbs__text--active {
  color: var(--standart-text-color);
  cursor: default;
}
.crumbs__text:not(.crumbs__text--active):hover,
.crumbs__text:not(.crumbs__text--active):focus {
  color: var(--main-color);
  transition: var(--crumbs-hover-animation);
}
.info {
  display: grid;
  grid-template-rows: min-content 1fr;
  grid-row-gap: var(--info-padding);
  height: 100%;
  padding-top: var(--info-padding);
  background-color: var(--data-list-color);
  background-color: var(--back-color);
}
.info__list {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: min-content;
  grid-gap: 20px;
  padding: var(--standart-side-padding) var(--info-padding);
  list-style: none;
  overflow-X: auto;
  background-color: var(--data-list-color);
}
@media (min-width: 1200px) {
  .info__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 900px) {
  .info__list {
    grid-template-columns: 1fr;
  }
}
.info__item {
  height: 100%;
  border-radius: 10px;
  padding: 20px 0 30px 0;
  min-width: min-content;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: stretch;
  box-shadow: var(--shadow);
}
.info__item:nth-child(6n - 6) {
  background-color: var(--inner-data-n1-color);
}
.info__item:nth-child(6n - 5) {
  background-color: var(--inner-data-n2-color);
}
