@media (max-width: 600px) {
  .profil__data {
    padding-top: calc(100px / 2 + 10px);
  }
}
.profil__person-image {
  position: absolute;
  top: calc(-200px / 2);
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid var(--back-color);
  box-shadow: -10px 10px 20px rgba(0, 0, 0, 0.4);
}
@media (max-width: 900px) {
  .profil__person-image {
    height: 150px;
    width: 150px;
    top: calc(-150px / 2);
  }
}
@media (max-width: 600px) {
  .profil__person-image {
    height: 100px;
    width: 100px;
    top: calc(-100px / 2);
  }
}
.profil__block {
  width: 100%;
  background-color: var(--back-color);
  box-shadow: var(--shadow);
