  color: var(--sub-text-color);
  font-weight: 500;
  text-align: center;
}
.profil__list-item {
  position: relative;
  display: grid;
  grid-template-columns: minmax(min-content, max-content) 1fr minmax(50px, max-content);
  grid-template-rows: 1fr;
  grid-template-areas: 'title line description';
  grid-gap: 10px;
