}
.profil__list-item::after {
  content: '';
  grid-area: line;
  align-self: center;
  width: 100%;
  height: 2px;
  border-top: 2px dashed var(--crumbs-arrow-color);
  top: 50%;
  transform: translateY(50%);
  left: 0;
}
.profil__title {
  grid-area: title;
  position: relative;
  font-size: 18px;
  color: var(--standart-text-color);
  font-weight: 400;
  text-align: left;
}
.profil__title::after {
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: tomato;
}
.profil__description {
  word-break: break-all;
  grid-area: description;
  font-size: 18px;
  color: var(--standart-text-color);
  font-weight: 400;
