  grid-row-gap: 10px;
  height: 100%;
}
.info-table__wrap {
  max-height: 100%;
  overflow: auto;
}
.info-table__table {
  position: relative;
  width: 100%;
  min-width: 100%;
  border-spacing: 0;
  text-align: center;
  margin: 0;
  padding: 0;
  overflow: auto;
  table-layout: fixed;
  border-collapse: collapse;
  color: var(--standart-text-color);
}
.info-table__table thead {
  z-index: var(--index-table-header);
  position: sticky;
  top: 0;
  background-color: var(--back-color);
  box-shadow: var(--main-table-head-shadow);
}
.info-table__table th {
  width: 120px;
  padding: 7px;
  font-weight: 500;
  font-size: 17px;
  opacity: 1;
}
.info-table__table > tbody > tr > td {
  padding: 10px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
}
.info-table__table > tbody > tr > td > a {
  text-decoration: none;
  color: var(--standart-text-color);
  padding: 10px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
}
.info-table__table > tbody > tr > td:hover > a,
.info-table__table > tbody > tr > td:focus > a {
  color: var(--main-color);
}
.info-table__table > tbody > tr:hover {
  padding: 20px;
  box-shadow: var(--main-table-shadow);
}
.info-table__table td:hover::after {
  content: "";
  position: absolute;
  box-shadow: var(--main-table-shadow);
  left: 0;
  top: 0;
  height: 100%;
  border-radius: 8px;
  width: 100%;
  z-index: -1;
}
.contacts-list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 20px;
}
.contacts-list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: var(--footer-contacts-list-icon-size);
  height: var(--footer-contacts-list-icon-size);
  box-shadow: var(--shadow);
  background-color: transparent;
  cursor: pointer;
}
.contacts-list__item:hover {
  box-shadow: var(--footer-contacts-list-icon-hover-shadow);
}
.contacts-list__link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: var(--standart-text-color);
  font-size: 25px;
  border-radius: 50%;
}
.contacts-list__link:hover {
  color: var(--standsrt-white);
}
.contacts-list__link--facebook:hover {
  background: #3b5998;
}
.contacts-list__link--twitter:hover {
  background: #1da1f2;
}
.contacts-list__link--instagram:hover {
  background: linear-gradient(135deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}
.preloader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: var(--index-redirect-preloader);
  background-color: var(--preloader-background-color);
  display: flex;
  transition: all 0.4s ease;
  opacity: 0;
  pointer-events: none;
}
.preloader--load {
  opacity: 1;
  pointer-events: all;
}
.preloader__loader {
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.preloader__text {
  position: absolute;
  width: var(--preloader-width);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-family: var(--basic-font);
  font-size: var(--preloader-font-size);
  font-weight: var(--preloader-font-weight);
  color: var(--standart-text-color);
  line-height: var(--preloader-font-line-height);
}
.preloader__text::before,
.preloader__text::after {
  content: "";
  display: block;
  width: var(--preloader-animation-element-size);
  height: var(--preloader-animation-element-size);
  background: var(--main-color);
  position: absolute;
  animation: load 1s infinite alternate ease-in-out;
}
.preloader__text::before {
  top: 0;
}
.preloader__text::after {
  bottom: 0;
}
