:root {
  --green: #2ccf6d;
}
.no-page {
  display: grid;
  grid-template-columns: 1fr minmax(200px, 400px);
  align-items: center;
  padding: 40px 0;
}
@media (max-width: 900px) {
  .no-page {
    padding: 20px 0;
    grid-template-columns: 1fr;
    grid-template-rows: min-content, 1fr;
  }
}
.no-page__info {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  gap: 10px;
}
@media (max-width: 900px) {
  .no-page__info {
    grid-row-start: 1;
  }
}
.no-page__404 {
  color: var(--standart-text-color);
  font-weight: bold;
  font-size: 22px;
}
.no-page__text {
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  color: var(--standart-text-color);
}
.no-page__btn {
  z-index: 1;
  overflow: hidden;
  background: transparent;
  position: relative;
  padding: 8px 50px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1em;
  letter-spacing: 2px;
  text-decoration: none;
  transition: 0.2s ease;
  font-weight: bold;
  border: 4px solid var(--green);
  color: var(--standart-text-color);
}
.no-page__btn:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: var(--green);
  z-index: -1;
  transition: 0.2s ease;
}
.no-page__btn:hover {
  color: var(--standsrt-white);
  background: var(--green);
  transition: 0.2s ease;
}
.no-page__btn:hover:before {
  width: 100%;
}
.info-controls {
  width: 100%;
  padding: 0 var(--standart-side-padding);
}
.info-controls__wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
}
.info-controls__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
.info-controls__button {
  padding: 10px 16px;
  color: var(--standart-text-color);
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
}
.info-controls__title {
  font-family: var(--basic-font);
  font-weight: 500;
  font-size: 22px;
  color: var(--sub-text-color);
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  display: table;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup.four {
  z-index: 5;
  transform: scale(1);
  background: rgba(0, 0, 0, 0.6);
  transition: background 0.4s ease;
  pointer-events: all;
}
.popup.four .popup__wrap {
  animation: blowUpModal 0.9s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}
.popup.four.out {
  background: transparent;
  pointer-events: none;
}
.popup.four.out .popup__wrap {
  animation: blowUpModalTwo 0.9s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}
.popup__wrap {
  border-radius: 20px;
  width: 90%;
  height: 70%;
  background: var(--back-color);
  box-shadow: var(--shadow);
  padding: 20px;
  z-index: 1;
}
.popup__close-button {
  width: 50px;
  height: 50px;
  padding: 10px;
  color: var(--standart-text-color);
}
@keyframes blowUpModal {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes blowUpModalTwo {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
